import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { FaLinkedinIn, FaGithub, FaTwitter, FaMedium } from 'react-icons/fa/';
import { animations } from '../../styles';

const Container = styled('div')`
  display: flex;
  justify-content: center;
  align-items: center;
  animation: ${animations.fadeInUp} 0.6s 0.6s ease both;
`;

const Href = styled('a')`
  cursor: pointer;
  box-shadow: none;
`;

const iconStyles = ({ theme }) => css`
  font-size: 20px;
  color: ${theme.colors.p600};
  margin: 0 10px;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s,
    transform ${theme.transitions.default};
  :hover {
    transform: translate3d(0px, -2px, 0px);
  }
`;

const StyledIcon = styled(({ component, ...props }) =>
  React.cloneElement(component, props)
)`
  ${iconStyles}
`;

const ICON_MAP = {
  Github: FaGithub,
  Medium: FaMedium,
  Twitter: FaTwitter,
  LinkedIn: FaLinkedinIn,
};

const mapLinksWithIcons = routes =>
  routes.map(({ label, url }) => {
    return {
      label,
      url,
      Icon: ICON_MAP[label],
    };
  });

function Social({ links }) {
  const icons = mapLinksWithIcons(links);

  return (
    <Container>
      {icons.map(({ Icon, url, label }, index) => (
        <Href
          aria-label={label}
          key={index}
          href={url}
          target="_blank"
          rel="noopener"
        >
          <StyledIcon component={<Icon />} />
        </Href>
      ))}
    </Container>
  );
}

Social.propTypes = {
  links: PropTypes.arrayOf(
    PropTypes.shape({
      url: PropTypes.string,
      label: PropTypes.string,
    })
  ).isRequired,
};

export default Social;
